@use "styles/stylesFunctions.scss" as f;
@use "styles/sassVariables" as v;

.table {
  margin-top: 15px;
  .table-header {
    background-color: v.$primary;
    color: #fff;
    font-size: 35px;
    padding: 25px 38px 22px;
  }

  .table-body {
    table {
      border-collapse: collapse;
      width: 100%;
    }
    td,
    th {
      border: 1px solid #ddd;
      padding: 15px;
    }
    tr:ntn-child(even) {
      background-color: ddd;
    }
  }
}
