@use "styles/stylesFunctions.scss" as f;
@use "styles/sassVariables" as v;

.table {
  .table-header {
    color: v.$secondary;
    font-size: 25px;
    padding: 25px 38px 22px;
  }

  .table-body {
    table {
      border-collapse: collapse;
      width: 100%;
    }
    td,
    th {
      border: 1px solid #ddd;
      padding: 15px;
    }
    tr {
      background-color: #f5f8ff;
    }
    tr:nth-child(1) {
      background-color: v.$secondary;
      color: #fff;
      text-align: center;
    }
  }
}
