@use "styles/stylesFunctions.scss" as f;
@use "styles/sassVariables" as v;

.wrapper {
  background-color: #f8f8f8;
  .container {
    padding-bottom: 100px;
    padding-top: 70px;

    .header-wrapper {
      display: flex;
      flex-direction: row;
      justify-content: space-between;
      align-items: center;
      .slider-header {
        color: v.$secondary;
        font-size: 35px;
        font-weight: 500;
      }

      .slider-sub-header {
        color: v.$secondary;
        font-size: 25px;
        font-weight: 500;

        margin-top: 40px;
        margin-bottom: 25px;
      }

      .booking {
        a {
          display: inline-block;
          padding: 16px 6vw;
          width: fit-content;
          background-color: v.$secondary;
          border: none;
          color: white;
          text-align: center;
          text-decoration: none;
          border-radius: 5px;

          margin-top: 7px;

          transition: v.$transition;

          &:hover {
            background-color: v.$primary;
            border: none;
            color: white;
            transform: scale(0.9);
          }
        }
      }
    }
  }
}
